import UserVIP from "./userVIP-model.js";
const userVIP = new UserVIP();
export default {
	data() {
		return {
			vipType: [
				{
					type: '3',
					name: '年度会员',
					content: '年度卡会员享受更多权益'
				},
				{
					type: '2',
					name: '季度卡会员',
					content: '季度卡会员享受更多权益'
				},
				{
					type: '1',
					name: '月卡会员',
					content: '月卡会员享受更多权益'
				}
			],
			userInfo: {},
			payData: {}
		};
	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			// that.wx_login(() => {
			// 	that.getUserInfo(() => {
			// 		callBack && callBack();
			// 	})
			// })
		},
		// 获取支付所需参数
		openVIP(e) {
			const that = this
			let type = userVIP.get_data_set(e, "type");
			userVIP.getPayInfo({
				openid: that.userInfo.openid,
				member_type: type
			}, (res) => {
				if (res.code == '4000') {
					that.payData = res.data
					// 执行支付
					that.runPay()
				}
				// callBack && callBack();
			})
		},
		runPay() {
			// 仅作为示例，非真实参数信息。
			const that = this
			const payData = that.payData
			uni.requestPayment({
				provider: 'wxpay',
				timeStamp: payData.timeStamp,
				nonceStr: payData.nonceStr,
				package: payData.package,
				signType: payData.signType,
				paySign: payData.paySign,
				success: function (res) {
					console.log('success:' + JSON.stringify(res));
					console.log(res)
					// if (res.errMsg == "requestPayment:ok") {
						that.getUserInfo()
					// }
					
				},
				fail: function (err) {
						console.log('fail:' + JSON.stringify(err));
				}
			});
		},
		getUserInfo(callBack) {
			const that = this
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					var code = loginRes.code;
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							userVIP.login({
								code: code,
								role: that.userInfo.role, // 角色
								portrait: infoRes.userInfo.avatarUrl,
								nickname: infoRes.userInfo.nickName
							}, (res) => {
								// console.log(res)
								if (res.code == 4000) {
									that.userInfo = res.data
									that.$store.commit('updateUserInfo', that.userInfo);
								}
								callBack && callBack();
							})
						}
					})
				}
			});
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		// let shareData = {
		// 	title: '',
		// 	path: `pages/index/index`,
		// 	imageUrl: ''
		// }
		return userVIP.onShareAppMessage({});
	}
}
